import Header from '../../components/Header'
import BottomBar from '../../components/BottomBar'
import {queryCollectionList, requestDeleteCollection} from '../../api/API'
import { MessageBox } from 'mint-ui'

let timer
let stopeTimer = false
export default {
  name: 'collection',
  data () {
    return {
      page: {
        page_no: 1,
        page_size: 20,
        total: 0,
        page_count: 1
      },
      pageData: [],
      inited: false
    }
  },
  components: {
    BottomBar,
    Header
  },
  mounted () {
    stopeTimer = false
    this.pages = []
    this.loadData()
  },
  beforeDestroy () {
    stopeTimer = true
    if (timer !== null) {
      clearInterval(timer)
      timer = null
    }
  },
  methods: {
    async loadData () {
      let res = await queryCollectionList(this.$store.state.User.token)
      if (res.code === 200) {
        this.page = res.data.page
        this.pageData = res.data.page_data
      }
      this.inited = true
      this.updataIndexs()
      if (stopeTimer === false) {
        this.nextLoop()
      }
    },
    nextLoop () {
      timer = setTimeout(this.loadData, 5000)
    },
    updataIndexs () {
      this.$store.dispatch('Stock/updateMarketIndex')
    },
    deleteCollect (id) {
      let _t = this
      MessageBox.confirm('确认将股票从自选中移除?').then(async () => {
        let res = await requestDeleteCollection(this.$store.state.User.token, id)
        if (res.code === 200) {
          _t.loadData()
        }
      }, () => {})
    }
  }
}



